
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

import {configs} from 'api'
import wx from 'weixin-js-sdk'
export default {
name: 'app',
components:{
ViewBox,
Tabbar,
TabbarItem
},
watch: {//使用watch 监听$router的变化
$route(to, from) {
if(to.meta.index > 0){
if( to.meta.index < from.meta.index){
this.transitionName = 'slide-right';
}else{
this.transitionName = 'slide-left';
}
}else if(to.meta.index == 0 && from.meta.index > 0){
this.transitionName = 'slide-right';
}
}
},
created () {
this.wxShare(this.$route.meta.title, '推荐一个超级好用的省钱工具')
},
data() {
return{transitionName: '',isIos:false}
},
methods: {
getDevice(){
if (navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)) {
this.isIos = true;
}
}
},
mounted () {
this.$Mui.init({
swipeBack: true 
})
this.getDevice()
}
}



